import { isEqual } from 'lodash';
import { memo } from 'react';
import { Navigate, useLocation } from 'react-router-dom';
import { getSessionItem, setSessionItem } from '../helpers';


const PrivateRoute = ({ children }) => {
    const { pathname, search } = useLocation();
    let ok =  getSessionItem('ok_bosque', false);
    setSessionItem('lastPath', pathname + search );
    if(ok == "false"){
        ok = false;
    }
    if(ok == "true"){
        ok = true;
    }


    return ok
        ? children
        : <Navigate to="/login" />
}

export default memo(PrivateRoute,isEqual);
