import { Link } from 'react-router-dom';
import { startLogin } from '../../actions/auth';
import { useDispatch } from 'react-redux';
import { useForm } from '../../hooks/useForm';
import '../../styles/styles.scss';
export const LoginScreen = () => {


    const dispatch = useDispatch();


    const [ formLoginValues, handleLoginInputChange ] = useForm({
        lUsername: '',
        lPassword: ''
    });
    const {   lUsername, lPassword } = formLoginValues;

    const handleLogin = ( e ) => {
        e.preventDefault();
        dispatch( startLogin( lUsername, lPassword ) );
    }




    return (


                <div className="container w-75 bg-primary mt-5 rounded shadow">
                <div className="row align-items-center align-items-stretch">
                <div className="col d-none d-lg-block col-md-5 col-lg-5 col-xl-6 rounded bg">

                </div>
                <div className="col bg-white p-5 col-lg-7 col-xl-6 rounded-end">
                    <div className="text-end">
                    <img src="img/logo.jpg" alt style={{width: 48}} />
                    </div>
                    <h2 className="fw-bold text-center pt-5 mb-5">Bienvenido</h2>
                    {/*
                            Formulario de login
                        */}
                    <form onSubmit={ handleLogin }  autoComplete="off">
                    <div className="mb-4">
                        <label htmlFor="username" className="form-label">Nombre de usuario</label>
                        <input type="text" className="form-control"  placeholder="Username"
                                name="lUsername"
                                value={ lUsername }
                                onChange={ handleLoginInputChange } />
                    </div>
                    <div className="mb-4">
                        <label htmlFor="password" className="form-label">Contrasea</label>
                        <input type="password" className="form-control" placeholder="Contrasea"
                                name="lPassword"
                                value={ lPassword }
                                onChange={ handleLoginInputChange } />
                    </div>

                    <div className="d-grid">
                        <button type="submit" value="Login" className="btn btn-primary">Iniciar sesin</button>
                    </div>

                    </form>
                    {/*
                            Login con redes sociales
                        */}
                    <div className="container w-100 my-5">
                    <div className="row my-3 text-center">
                        <div className="col-12">Nadie es perfecto. Por eso los lpices tienen goma.</div>
                    </div>
                    <div className="row my-3 text-center">
                    <div className="d-grid">
                        <Link to="/" className="" style={{color: "green", textDecoration: "none" }} >Pagina principal</Link>
                    </div>

                    </div>
                    </div>
                    {/*
                        */}
                </div>
                </div>
                </div>

            )
        }







