import http from '../http-common-token';

const getAll = () => {
    return http.get('user');
}

const getById = (id) => {
    return http.get(`user/${id}`);
}


const update = (id, data) => {
    return http.put(`user/${id}`, data);
}
const remove = (id) => {
    return http.delete(`user/${id}`);
}

export default
{

    getAll,
    getById,
    update,
    remove
}
