import http from '../http-common-token';

const getAll = () => {
    return http.get('videos');
}

const getById = (id) => {
    return http.get(`videos/${id}`);
}

const create = (data) => {
    return http.post('videos' , data);
}
const update = (id, data) => {
    return http.put(`videos/${id}`, data);
}
const remove = (id) => {
    return http.delete(`videos/${id}`);
}

export default
{

    getAll,
    create,
    getById,
    update,
    remove
}
