import { memo, Suspense } from "react";
import { BrowserRouter, Route, Routes } from "react-router-dom";

import { useDispatch, useSelector } from "react-redux";
import { useEffect } from "react";

import { default as PrivateRoute } from "./PrivateRoute";
import { default as PublicRoute } from "./PublicRoute";

import { startChecking } from "../actions/auth";
import "../styles/loanding.scss";
import { routes_admin, routes_client } from "./routes";
import { LoginScreen } from "../components/login/LoginScreen";
import { MaterialesProvider } from "../context/MaterialesProvider";
import { useMediaQuery, useTheme } from "@mui/material";
import { isEqual } from "lodash";

const AppRouter = () => {
    const dispatch = useDispatch();
    const { checking } = useSelector((state) => state.auth);
    const theme = useTheme();
    const isMobile = useMediaQuery(theme.breakpoints.down("md"));

    useEffect(() => {
        dispatch(startChecking());
    }, [dispatch]);

    if (checking) {
        return (
            <div className="loanding">
                {!isMobile ? (
                    <img
                        className="logos-img"
                        src="../img/logo.jpg"
                        style={{ height: "50%", width: "50%" }}
                    />
                ) : (
                    <img
                        className="logos-img"
                        src="../img/logo.jpg"
                        style={{ height: "50%", width: "100%" }}
                    />
                )}
            </div>
        );
    }

    return (
        <Suspense
            fallback={
                <div className="loanding">
                    {!isMobile ? (
                        <img
                            className="logos-img"
                            src="../img/logo.jpg"
                            style={{ height: "50%", width: "50%" }}
                        />
                    ) : (
                        <img
                            className="logos-img"
                            src="../img/logo.jpg"
                            style={{ height: "50%", width: "100%" }}
                        />
                    )}
                </div>
            }
        >
            <BrowserRouter>
                {" "}
                <MaterialesProvider>
                    <Routes>
                        <Route
                            path="/login"
                            element={
                                <PublicRoute>{<LoginScreen />}</PublicRoute>
                            }
                        />

                        {routes_client.map(({ path, Component }) => (
                            <Route
                                key={path}
                                path={path}
                                element={
                                    <PublicRoute>{<Component />}</PublicRoute>
                                }
                            />
                        ))}

                        {routes_admin.map(({ path, Component }) => (
                            <Route
                                key={path}
                                path={path}
                                element={
                                    <PrivateRoute>{<Component />}</PrivateRoute>
                                }
                            />
                        ))}
                    </Routes>
                </MaterialesProvider>
            </BrowserRouter>
        </Suspense>
    );
};
export default memo(AppRouter,isEqual);
