import http from '../http-common-token';

const getAll = () => {
    return http.get('bolsas'); 
}

const getById = (id) => {
    return http.get(`bolsas/${id}`);
}

const create = (data) => {
    return http.post('bolsas' , data);
}
const update = (id, data) => {
    return http.put(`bolsas/${id}`, data);
}
const remove = (id) => {
    return http.delete(`bolsas/${id}`);
}

export default
{
  
    getAll,
    create,
    getById,
    update,
    remove
}
