import { isEqual } from 'lodash';
import { memo } from 'react';
import { Navigate } from 'react-router-dom';
import { getSessionItem } from '../helpers';


const PublicRoute = ({ children }) => {

    let ok =  getSessionItem('ok_bosque', false);
    if(ok == "false"){
        ok = false;
    }
    if(ok == "true"){
        ok = true;
    }


const lastPath =  getSessionItem('lastPath','/admin/anuncios');
return ok
        ? <Navigate to={lastPath} />
        : children
}


export default memo(PublicRoute ,isEqual);
