import { fetchSinToken, fetchConToken } from '../helpers/fetch';
import { types } from '../types/types';
import Swal from 'sweetalert2';
import { APP_DEBUG_MODE, clearSessionItems, getSessionItem, setSessionItem } from '../helpers';



export const startLogin = ( username, password ) => {
    return async( dispatch ) => {

        const resp = await fetchSinToken( 'auth/login', { username, password }, 'POST' );
        const body = await resp.json();
        APP_DEBUG_MODE && console.log(body);
        if( body.ok ) {



            setSessionItem('name', body.user.name );
            setSessionItem('token', body.token );
            setSessionItem('token-init-date', new Date().getTime() );
            setSessionItem('ok_bosque',true  );

            dispatch( login({
                uid: body.user.id,
                name: body.user.name
            }) )
        } else {
            Swal.fire('Error', body.Message, 'error');
        }


    }
}

export const startRegister = (name, username, password, password_confirmation) => {
    return async( dispatch ) => {
        APP_DEBUG_MODE &&  console.log(name + username+ password+password_confirmation);
        const resp = await fetchSinToken( 'auth/register', { name ,username, password,  password_confirmation}, 'POST' );
        const body = await resp.json();
        APP_DEBUG_MODE &&  console.log(body);
        if( body.ok ) {



            setSessionItem('name', body.user.name );
            setSessionItem('token', body.token );
            setSessionItem('token-init-date', new Date().getTime() );
            setSessionItem('ok_bosque',true  );


            Swal.fire('Success',"Usuario creado", 'success');
            dispatch( login({
                uid: body.user.id,
                name: body.user.name
            }) )
        } else {

            Swal.fire('Error', body.Message, 'error');
        }


    }
}

export const startChecking = () => {
    return async(dispatch) => {
        const token =  getSessionItem('token', '');
        const resp = await fetchConToken( 'user' ,{"token": token} ,'POST');
        const body = await resp.json();
        APP_DEBUG_MODE &&  console.log(body);
        if( body.user ) {
            setSessionItem('ok_bosque',true );
            dispatch( login({
                uid: body.user.id,
                name: body.user.name
            }) )
        } else {
            clearSessionItems();
            dispatch( checkingFinish() );

        }
    }
}

const checkingFinish = () => ({ type: types.authCheckingFinish });



const login = ( user ) => ({
    type: types.authLogin,
    payload: user
});


export const startLogout = () => {
    return async( dispatch ) => {


        clearSessionItems();
        dispatch( eventLogout() );
        dispatch( logout() );
    }
}

const logout = () => ({ type: types.authLogout })
export const eventLogout =() => ({ type: types.eventLogout });
