
import http from '../http-common-token';

const getAll = () => {
    return http.get('materiales');
}

const getById = (id) => {
    return http.get(`materiales/${id}`);
}
const getByCategoria = (data) => {
    return http.get(`materialesporcategoria`, data);
}
const getByCategoriaPagination = (data) => {

    return http.get(`paginacioncategorias`, data);
}


const create = (data) => {
    return http.post('materiales' , data);
}
const update = (id, data) => {
    return http.put(`materiales/${id}`, data);
}
const remove = (id) => {
    return http.delete(`materiales/${id}`);
}

export default
{
    getByCategoriaPagination,
    getByCategoria,
    getAll,
    create,
    getById,
    update,
    remove
}
