import { LazyExoticComponent } from 'react';
import { lazy } from 'react'

type JSXComponent = () => JSX.Element;

interface Route {
    path: string;
    to: string;
    Component: LazyExoticComponent<JSXComponent> | JSXComponent,
    name: string;
}

export const routes_client: Route[] = [

    {
        path: '/*',
        to: '/',
        Component: lazy( () => import(/* webpackChunkName: "LazyLayout_client" */ './ClientRoutes ') ),
        name: 'Cliente'
    }


]

export const routes_admin: Route[] = [




    {

        path: '/admin/*',
        to: 'admin',
        Component: lazy( () => import(/* webpackChunkName: "LazyLayout_admin" */ './DashboardRoutes') ),
        name: 'Admin'
    }

]
