import axios from "axios";
import { useState, useEffect, createContext, SetStateAction,  ChangeEvent } from 'react';
import queryString from "query-string";
import { useLocation} from "react-router-dom";
import { Material } from '../components/materiales/MaterialCard';
import VideosServie from "../services/VideosServie";
import BlogsServie from "../services/BlogsServie";
import BolsaServie from "../services/BolsaServie";
import NosotrosServie from "../services/NosotrosServie";
import UserServie from "../services/UserServie";
import CategoriasServie from "../services/CategoriasServie";
import MaterialesServie from "../services/MaterialesServie";
import { API_BASE_URL, APP_DEBUG_MODE, useApi } from "../helpers";

export interface Props {
    children: () => JSX.Element,



}

export interface Video {
    id: number;
    img?: string;
    title?: string;
    url?: string;
    content?: string;
    visible?: boolean;

}
export interface Blog {
    id: number;
    img?: string;
    title?: string;
    url?: string;
    description?: string;
    visible?: boolean;

}
export interface Nosotros  {
    name?: string;
    descripcion?: string;
    url?: string;
    whatsapp?: string;
    face?: string;
    municipio?: string;
    estado?: string;
    domicilio?: string;
    phone?: string;
    correo?: string;


};
const initialNosotrosState = {
    name: "",
    descripcion: "",
    url: "",
    whatsapp: "",
    face: "",
    municipio: "",
    estado: "",
    domicilio:"",
    phone: "",


};
export interface Bolsa {
    id: number;
    img?: string;
    title?: string;
    url?: string;
    content?: string;
    visible?: boolean;

}
export interface Bolsa {
    id: number;
    img?: string;
    title?: string;
    url?: string;
    content?: string;

}
export interface Categorias {
    id: number;
    img?: string;
    title?: string;
    url?: string;
    description?: string;

}
export interface User{
    id: number,
    name: string,

}

export interface MaterialContextProps {
     categoria: Number;
    handleChangeCategoria:  (e: {
        target: {
            value: SetStateAction<number>;
        };
    }) => void;
    materiales: Material[];
    totalMateriales: number;
    handleChangePagina:(event: ChangeEvent<unknown>, page: number) => void;
    pagina: number;
    totalPaginas: number;
    blogs: Blog[],
    videos: Video[],
    setBlogs: React.Dispatch<SetStateAction<never[]>>,
    setVideos: React.Dispatch<SetStateAction<never[]>>,
    bolsas: Bolsa[],
    setBolsas: React.Dispatch<SetStateAction<never[]>>,
    currentNosotros: Nosotros,
    setCurrentNosotros: React.Dispatch<SetStateAction<Nosotros>>,
    user: User[],
    setUser: React.Dispatch<SetStateAction<never[]>>,
    material: Material[],
    setMaterial: React.Dispatch<SetStateAction<never[]>>,
    categorias: Categorias[],
    setCategorias: React.Dispatch<SetStateAction<never[]>>,
}
const MaterialesContext = createContext({} as MaterialContextProps);



const MaterialesProvider = ({ children }: Props) => {

    const [categoria, setCategoria] = useState(0);
    const [materiales, setMateriales] = useState([]);
    const [pagina, setPagina] = useState(1);
    const [totalMateriales, setTotalMateriales] = useState(0);

    const location = useLocation();

    const { q = '' } = queryString.parse(location.search);

    useEffect(() => {
        const consultarAPI = async () => {
            await axios({
                method: 'get',
                url:  API_BASE_URL + 'paginacioncategorias',
                headers: {
                    'Content-Type': 'application/json',
                },
                params: {
                    pagina: 1,
                    por_pagina: 20,
                    categoria_id: categoria,
                    nombre: q
                }
            })
                .then((response) => {
                    setMateriales(response.data.data);
                    setTotalMateriales(response.data.conteo);
                    setPagina(1);
                    APP_DEBUG_MODE   &&  console.log(response.data);
                })
                .catch((error) => {
                    APP_DEBUG_MODE   &&  console.log(error);
                }
                );

        };
        consultarAPI();
    }, [categoria, q]);

    useEffect(() => {
        const consultarAPI = async () => {
            await axios({
                method: 'get',
                url:  API_BASE_URL + 'paginacioncategorias',
                headers: {
                    'Content-Type': 'application/json',
                },
                params: {
                    pagina: pagina,
                    por_pagina: 20,
                    categoria_id: categoria,
                    nombre: q
                }
            })
                .then((response) => {
                    setMateriales(response.data.data);
                    setTotalMateriales(response.data.conteo);
                    APP_DEBUG_MODE   && console.log(response.data);
                })
                .catch((error) => {
                    APP_DEBUG_MODE   && console.log(error);
                }
                );

        };
        consultarAPI();
    }, [pagina, q]);

    const handleChangeCategoria = (e: { target: { value: SetStateAction<number>; }; }) => {
        setCategoria(e.target.value);
    };

    const handleChangePagina = (event: ChangeEvent<unknown>, page: number) => {
        setPagina(page);
    };

    const totalPaginas = Math.ceil(totalMateriales / 20);
    const { get: blogsGet,  data: blogsData } = useApi('blogs');

    const [blogs, setBlogs] = useState([]);
    const [videos, setVideos] = useState([]);
    const [bolsas, setBolsas] = useState([]);
    const [currentNosotros, setCurrentNosotros] = useState<Nosotros>(initialNosotrosState as Nosotros);
    const [user, setUser] = useState([]);
    const [categorias, setCategorias] = useState([]);


    const [material, setMaterial] = useState([]);




    useEffect(() => {
        retriveBlogs();
        retriveVideos();
        retrivebolsa();
        retriveUsers();
        retriveCategorias();
        retriveMaterials();
        getNosotros();
    }, []);

    useEffect(() => {
        if(blogsData != null ){
             setBlogs(blogsData);
        }
    }, [blogsData]);


    const retriveBlogs = () => {
        blogsGet(`blogs`);


    };
    const retriveCategorias = () => {
        CategoriasServie.getAll()
            .then((response) => {
                setCategorias(response.data);
                APP_DEBUG_MODE   &&  console.log(response.data);
            })
            .catch((err) => {
                alert("Ocurrio un error");
                APP_DEBUG_MODE   &&   console.log(err);
            });
    };
    const retriveVideos = () => {
        VideosServie.getAll()
            .then((response) => {
                setVideos(response.data);
                APP_DEBUG_MODE   &&    console.log(response.data);
            })
            .catch((err) => {
                alert("Ocurrio un error");
                APP_DEBUG_MODE   && console.log(err);
            });
    };

    const retrivebolsa = () => {
        BolsaServie.getAll()
            .then((response) => {
                setBolsas(response.data);
                APP_DEBUG_MODE   &&      console.log(response.data);
            })
            .catch((err) => {
                alert("Ocurrio un error");
                APP_DEBUG_MODE   &&      console.log(err);
            });
    };
    const getNosotros = () => {
        NosotrosServie.getById()
            .then((response) => {
                setCurrentNosotros({
                    name: response.data.name || "",
                    descripcion: response.data.descripcion || "",
                    url: response.data.url || "",
                    domicilio: response.data.domicilio || "",
                    estado: response.data.estado || "",
                    municipio: response.data.municipio || "",
                    phone: response.data.phone || "",
                    whatsapp: response.data.whatsapp || "",
                    face: response.data.face || "",
                    correo: response.data.correo || "",
                });
            })
            .catch((err) => {
                alert("Ocurrio un error");
                APP_DEBUG_MODE   &&   console.log(err);
            });
    };
    const retriveUsers = () => {
        UserServie.getAll()
            .then((response) => {
                setUser(response.data);
                APP_DEBUG_MODE   &&       console.log(response.data);
            })
            .catch((err) => {
                alert("Ocurrio un error");
                APP_DEBUG_MODE   &&      console.log(err);
            });
    };


    const retriveMaterials = () => {
        MaterialesServie.getAll()
            .then((response) => {
                setMaterial(response.data);
                APP_DEBUG_MODE   &&     console.log(response.data);
            })
            .catch((err) => {
                alert("Ocurrio un error");
                APP_DEBUG_MODE   &&  console.log(err);
            });
    };
    return (
        <MaterialesContext.Provider
            value={{
                material,
                setMaterial,
                categorias,
                setCategorias,
                user,
                setUser,
                currentNosotros,
                setCurrentNosotros,
                setBolsas,
                setBlogs,
                setVideos,
                blogs,
                bolsas,
                videos,
                categoria,
                handleChangeCategoria,
                materiales,
                totalMateriales,
                totalPaginas,
                handleChangePagina,
                pagina,

            }}
        >
            {children}
        </MaterialesContext.Provider>
    );
};

export { MaterialesProvider };

export default MaterialesContext;
